@for $i from 1 through $cols-number {
  .col {
    &--offset-#{$i} {
      margin-left: percentage($i/$cols-number); }
    &--offset-xxs-#{$i} {
      @include xxsmin {
        margin-left: percentage($i/$cols-number); } }
    &--offset-xs-#{$i} {
      @include xsmin {
        margin-left: percentage($i/$cols-number); } }
    &--offset-sm-#{$i} {
      @include smmin {
        margin-left: percentage($i/$cols-number); } }
    &--offset-md-#{$i} {
      @include mdmin {
        margin-left: percentage($i/$cols-number); } }
    &--offset-lg-#{$i} {
      @include lgmin {
        margin-left: percentage($i/$cols-number); } }
    &--offset-xl-#{$i} {
      @include xlmin {
        margin-left: percentage($i/$cols-number); } } } }
